


const defaultState = {
    typetitles:[],
    onetitle:[],
    title:"武汉1803",
    data:"你们已经精通react了吗",
    inputtitle:[],
    yzmcode:"",
    shoucang:[],
    carcode:'',
    carresult:[],
}

export default (state=defaultState,action)=>{
    switch(action.type){
        case "typetitle":
        return {...state,...{typetitles:action.data}}
        break;

//changeoneTitle
//inputValue
        case "changeoneTitle":
        return {...state,...{onetitle:action.data}}
        break;

        case "inputValue1":
        return {...state,...{inputtitle:action.data}}
        break;
//changeyzmcode
        case "changeyzmcode":
        return {...state,...{yzmcode:action.data}}
        break;
//changeshoucang
        case "changeshoucang":
        return {...state,...{shoucang:action.data}}
        console.log(action.data)
        break;
//shoppingcarcode
        case "shoppingcarcode":
        return {...state,...{carcode:action.data}}
        console.log(action.data)
        break;
//shoppingcarresult
        case "shoppingcarresult":
        return {...state,...{carresult:action.data}}
        console.log(action.data)
        break;
//changecarcode
        case "changecarcode":
        return {...state,...{carcode:action.data}}
        console.log(action.data)
        break;

        default:
        return state;
        break;
    }
}


