

import React,{Component} from "react";
import {Route,Switch,Redirect} from "react-router-dom";
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'; // ES6
import { NavBar, Icon,Button, List,Toast, WhiteSpace, WingBlank,Carousel,PullToRefresh,Modal } from 'antd-mobile';
import "./fenlei.scss";
const Item = List.Item;
const alert = Modal.alert;
import {connect} from "react-redux";
import {ajaxSend,typetitle,shoppingcarcode} from "../../actions/index"
@connect(
    state=>(
        {
            ...state
        }
    )
)
export class Fenlei extends Component{
    state={
        refreshing: false,
    }

    gotobuyvip=(trackId)=>{
        console.log("点击购买"+trackId);
        var that=this;
        const {dispatch,match} = this.props;
        if(localStorage.phonenum){
            dispatch(ajaxSend({
                config:{
                    url:"/shoppingcar?trackId="+trackId+"&phonenum="+localStorage.phonenum,
                    method:"get",
                   
                },
                success(data){
                    dispatch(shoppingcarcode(data.msg))
                    // console.log("ajaxSend")
                    console.log(data);
                    that.refs.mineshadow.style.top="45%";
                    var count=0;
                   
                    var timer=setInterval(function(){
                              count++;
                              if(count==2){
                                  that.refs.mineshadow.style.top="-10%";
                                  clearInterval(timer)
                                  count=0;
                              }
                    },500)
                }
            }))
    
        }else{
            const {history}=this.props;
            //;
            const alertInstance = alert('提示', '你还未登录，是否前往登录？', [
                { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                { text: '前往登陆', onPress: () => history.push("/app/mine") },
              ]);
              setTimeout(() => {
                // 可以调用close方法以在外部close
                console.log('auto close');
                alertInstance.close();
              }, 500000);
        }

    }
    gotoshoucang=(trackId)=>{
        console.log("点击收藏"+trackId);
        const {dispatch,match} = this.props;
        var that=this;
        if(localStorage.phonenum){
            dispatch(ajaxSend({
                config:{
                    url:"/shoucang?trackId="+trackId+"&phonenum="+localStorage.phonenum,
                    method:"get",
                   
                },
                success(data){
                    dispatch(shoppingcarcode(data.msg))
                    // console.log("ajaxSend")
                    console.log(data);
                    that.refs.mineshadow.style.top="45%";
                    var count=0;
                   
                    var timer=setInterval(function(){
                              count++;
                              if(count==2){
                                  that.refs.mineshadow.style.top="-10%";
                                  clearInterval(timer)
                                  count=0;
                              }
                    },500)
                }
            }))
    
        }else{
            const {history}=this.props;
           
            const alertInstance = alert('提示', '你还未登录，是否前往登录？', [
                { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                { text: '前往登陆', onPress: () =>  history.push("/app/mine") },
              ]);
              setTimeout(() => {
                // 可以调用close方法以在外部close
                console.log('auto close');
                alertInstance.close();
              }, 500000);
        }
       
        
    }
   componentWillMount(){
    const {dispatch,match} = this.props;
    dispatch(ajaxSend({
        config:{
            url:"/getalltitle?type="+match.params.lei,
            method:"get",
           
        },
        success(data){
            dispatch(typetitle(data))
            console.log("ajaxSend")
            console.log(data);
        }
    }))
   }
    render(){
        const {match,history}=this.props;
        return(
            <div>
                <NavBar
                    mode="light"
                    icon={<Icon type="left"  style={{color:'#666666'}}/>}
                    onLeftClick={() => history.goBack()}
                    rightContent={[
                        
                        <Icon key="1" type="ellipsis" style={{color:'#666666'}}/>,
                    ]}
                >
                {match.params.lei?match.params.lei:"分类"}
                </NavBar>
                <h3 className="fenleih3">{}</h3>
                <p className="fenleip">{}</p>
                <ul className="my-list clearfix">
                    <PullToRefresh
                        damping={60}
                        ref={el => this.ptr = el}
                        style={{
                            overflow: 'auto',
                        }}
                        indicator='上拉可以刷新'
                        direction= 'down' 
                        refreshing={this.state.refreshing}
                        onRefresh={() => {
                            console.log("正在下拉刷新")
                            this.setState({ refreshing: true });
                            setTimeout(() => {
                                this.props.typetitles.reverse()
                                this.setState({ 
                                    refreshing: false ,
                                 
                                });
                            }, 1000);
                        }}
                    >
                     <ReactCSSTransitionGroup
                        transitionName={
                            {
                                enter:"enter",
                                leave:'leave',
                                appear:"appear"
                            }
                        }
                        transitionAppear={true}
                        transitionEnter={true}
                        transitionLeave={true}
                        transitionAppearTimeout={500}
                        transitionEnterTimeout={800}
                        transitionLeaveTimeout={800}
                    >
                {
                    this.props.typetitles.map((fen,index)=>{
                        return(
                                <div className="feileidiv" key={index}>
                                    <div className="feileidivimg" onClick={()=>history.push("/app/listen/"+fen.trackId+"?vip="+fen.vip)}><div className="fenleii" ><i className="iconfont icon-bofang1"></i></div><img src={fen.coverMiddle} alt=""/></div>
                                    <div className="feileidivdiv">
                                        <h4>{fen.title.substring(0,20)+"..."}</h4>
                                        <p>{fen.nickname}</p>
                                        <p className="feileidivp"><span className="feileidivpleft">订阅量{fen.playtimes}万</span>
                                        
                                        <span className="feileidivpright"
                                        onClick={fen.vip==1?()=>{this.gotobuyvip(fen.trackId)}:()=>{this.gotoshoucang(fen.trackId)}}

                                        >{fen.vip==1?<button>vip</button>:<button>订阅</button>}</span></p>
                                    </div>
                                </div>
                            )
                        })
                }
                </ReactCSSTransitionGroup>
                    </PullToRefresh>
                </ul>
                <div className="mineshadow" ref="mineshadow">{this.props.carcode}</div>
                <p className="feileiendp">----------------end----------------</p>
            </div>
        )
    }
}


