import React,{Component} from "react";
import {Route,Switch,Redirect,NavLink} from "react-router-dom";
 import "./foot.scss";
export  class Foot extends Component{
    state = {
        footList:[
            {txt:"首页",path:"/app/home",name:"wechat",icon:"icon-home-fill"},
            {txt:"我听",path:"/app/like",name:"favfill",icon:"icon-favfill"},
            {txt:"",path:"/app/listen",name:"bofang1",icon:"icon-bofang1"},
            {txt:"发现",path:"/app/found",name:"tansuob",icon:"icon-tansuob"},
            {txt:"我的",path:"/app/mine",name:"zuoweibeijing",icon:"icon-zuoweibeijing"},
        ]
    }
    render(){
        return(
            <div>
             
               <footer>
                    {
                        this.state.footList.map((foot,index)=>{
                            return (
                                <div className="icondiv" key={index}>
                                    <NavLink to={foot.path}  activeClassName="activeClassName" > 
                                        <i className={"iconfont "+foot.icon }></i>
                                        <span>{foot.txt}</span>
                                    </NavLink>
                                </div>
                            )
                        })
                        }
                </footer>
            </div>
        )
    }
}


