import React,{Component} from "react";
import {Route,Switch,Redirect} from "react-router-dom";
import { Carousel, WingBlank } from 'antd-mobile';
import "../utils/swiper-3.3.1.min.js"
import "./guide.scss"



export  class Guide extends Component{
  componentWillMount(){
        
    }  
    componentDidMount(){
        var mySwiper = new Swiper('.swiper-container',{
        
             })
    }

    componentDidUpdate(){
        var  mySwiper = new Swiper('.swiper-container',{
            
             })

    }
    gotohome=(index)=>{
        if(index==this.state.imgs.length-1){
            var {history}=this.props
            history.push("/app/home")
        }
    }
   
    state = {
        imgs:[
            require("../../assets/images/001.jpg"),
            require("../../assets/images/002.jpg"),
            require("../../assets/images/003.jpg"),
            require("../../assets/images/004.jpg"),
            require("../../assets/images/005.jpg"),
        ]
    }
    render(){
        //swiperOptions={{resistanceRatio:0,loop:false,autoPlay:1200,speed:1200}}
        return(
            <div>
                <div className="swiper-container" >
                        <div className="swiper-wrapper">
                    {
                        this.state.imgs.map((child,index)=>{
                            return (
                                <div key={index} onClick={()=>this.gotohome(index)} className="swiper-slide"  style={{eventTarget : 'container'}} >
                                    <img src={child} alt=""  style={{ display: 'inline-block', width: '100%', height:"100%" }} />
                                </div>
                            )
                        })
                    }
                    </div>
                    </div>
            </div>
        )
    }
}


