import React,{Component} from "react";
import {Route,Switch,Redirect} from "react-router-dom";
import "./mine.scss";
import { Drawer, List, NavBar, Icon,WhiteSpace,ActionSheet, WingBlank, Button, Toast, Modal  } from 'antd-mobile';

import {connect} from "react-redux";
import {ajaxgetyzm,changeyzmcode} from "../../actions";

const Item = List.Item;
const Brief = Item.Brief;
const prompt = Modal.prompt;
const PlaceHolder = ({ className = '', ...restProps }) => (
    <div className={`${className} placeholder`} {...restProps}></div>
  );

  const isIPhone = new RegExp('\\biPhone\\b|\\biPod\\b', 'i').test(window.navigator.userAgent);
  let wrapProps;
  if (isIPhone) {
    wrapProps = {
      onTouchStart: e => e.preventDefault(),
    };
  }
@connect(
    state=>(
        {
            ...state
        }
    )
)
export  class Mine extends Component{
    state = {
        disabled: false,
        // tuichu:localStorage.phonenum?'退出登录':''
        tishi:"",
       
      }
      tanchuceng=(msg)=>{
        this.setState({
            tishi:msg
        })
      this.refs.mineshadow.style.top="45%";
      var count=0;
      var that=this;
      clearInterval(timer)
      var timer=setInterval(function(){
                count++;
                if(count==2){
                    that.refs.mineshadow.style.top="-10%";
                    clearInterval(timer)
                    count=0;
                }
      },1000)

      }
      loginhide=()=>{
          console.log(this.refs.logindiv)
          this.refs.logindiv.style.display="none"
      }
      willlogin=()=>{
        this.refs.logindiv.style.display="block"
      }
      gotomyshoucang=()=>{
          var phonenum=localStorage.phonenum;
          if(phonenum){
                const {history}=this.props;
                history.push("/myshoucang?phonenum="+phonenum);
          }else{
              this.setState({
                  tishi:"您还未登录，请您先登录"
              })
            this.refs.mineshadow.style.top="45%";
            var count=0;
            var that=this;
            var timer=setInterval(function(){
                      count++;
                      if(count==2){
                          that.refs.mineshadow.style.top="-10%";
                          clearInterval(timer)
                          count=0;
                      }
            },1000)
            
          }
         

      }
      gotomine=()=>{
            console.log(this.props.yzmcode)
            console.log(this.refs.yzmInput.value)
            if(this.refs.yzmInput.value==this.props.yzmcode&&this.refs.yzmInput.value!=""){
                this.setState({
                    tishi2:""
                })
                const {history}=this.props
                history.push("/app/")
                console.log("登录成功")
                localStorage.phonenum=this.refs.phonenum.value;
            }else{
                this.setState({
                    tishi2:"验证码输入有误，请重新输入"
                })
            }
      }
      getyzm=()=>{
        var phonenumreg=/^[1]{1}[5,7,8,3,9]{1}[0-9]{9}$/ig;
        if(phonenumreg.test(this.refs.phonenum.value)==true&&this.refs.phonenum.value!=""){
          this.setState({
              tishi:""
            })
            const {dispatch} = this.props;
            dispatch(ajaxgetyzm({
                config:{
                    url:"/getyzm?phonenum="+this.refs.phonenum.value,
                    method:"get",
                },
                success(data){
                    console.log("ajaxSend")
                    console.log(data.code);
                    dispatch(changeyzmcode(data.code))
                    
                }
            }))
        }else{
          this.setState({
              tishi:"手机号格式错误"
            })
        }
      }
      userphone=()=>{
          console.log(this.refs.phonenum.value)
          var phonenumreg=/^[1]{1}[5,7,8,3,9]{1}[0-9]{9}$/ig;
          if(phonenumreg.test(this.refs.phonenum.value)==true){
            this.setState({
                tishi:""
              })
          }else{
            this.setState({
                tishi:"手机号格式错误"
              })
          }
      }
      constructor() {
        super();
        this.state = {
          clicked: 'none',
          clicked1: 'none',
          clicked2: 'none',
          tishi:"",
          tishi2:""
        };
      }

      realalert=()=>{
          localStorage.phonenum="";
      }
//maskClosable
      showActionSheet = () => {
          var that=this
        const BUTTONS = [ localStorage.phonenum?'退出登录':'您还未登录', '取消'];
        ActionSheet.showActionSheetWithOptions({
          options: BUTTONS,
          cancelButtonIndex: BUTTONS.length - 1,
          destructiveButtonIndex: BUTTONS.length - 2,
          // title: 'title',
        
          maskClosable: true,
          'data-seed': 'logId',
          wrapProps,
        
        },
       
        (buttonIndex) => {
            console.log(buttonIndex)
            if(buttonIndex==0){
                console.log(true)
               this.realalert()
            }else{
                console.log(false)
            }
          
          this.setState({ clicked: BUTTONS[buttonIndex] });

        });
      }
      
    render(){
        return(
            <div ref="bigminediv">
                <header>
                    <span className="my">我的</span>
                    <div className="icondiv">
                    <i className="iconfont icon-youxiang"></i>
                    <i onClick={this.showActionSheet} className="iconfont icon-shezhi"></i>
                    </div>
                </header>
                <div className="topdiv" onClick={this.willlogin} >
                    <span className="topspan"><i className="iconfont icon-user-girl"></i></span>
                    <div>
                        <div className="usernamediv">
                            <p className="username">{localStorage.phonenum?localStorage.phonenum:"您还未登录，请登录"}</p>
                        </div>
                    </div>
                </div>
                <div className="vipdiv">
                    <p className="vip1">vip会员</p>
                    <p className="vip2"> 精品臻选免费畅听<i className="iconfont icon-jiantouyou"></i></p>
                </div>
                <div className="mywallet">
                    <div>
                        <p>--</p>
                        <p className="fontp"  onClick={()=>{this.tanchuceng("没有任何购买商品")}}>已购</p>
                    </div>
                    <div>
                        <p>--</p>
                        <p className="fontp" onClick={this.gotomyshoucang}>我的收藏</p>
                    </div>
                    <div>
                        <p>--</p>
                        <p className="fontp"   onClick={()=>{this.tanchuceng("暂未开通")}}>直播喜钻</p>
                    </div>
                    <div>
                        <p>--</p>
                        <p className="fontp"   onClick={()=>{this.tanchuceng("您的颜值。。救命啊")}}>直播</p>
                    </div>
                    <div>
                        <p className="iconp"><i className="iconfont icon-wallet"></i></p>
                        <p className="fontp" onClick={()=>{this.tanchuceng("还有金山一座")}}>钱包</p>
                    </div>
                </div>
                <div className="myshow">
                    <div    onClick={()=>{this.tanchuceng("嚎叫未遂")}}>
                        <p><i className="iconfont icon-maikefeng"></i></p>
                        <p>我要录音</p>
                    </div>
                    <div    onClick={()=>{this.tanchuceng("摄像头瞎了")}}>
                        <p><i className="iconfont icon-zhibo"></i></p>
                        <p>我要直播</p>
                    </div>
                    <div   onClick={()=>{this.tanchuceng("还是看别人的吧")}}>
                        <p><i className="iconfont icon-bofang1"></i></p>
                        <p>作品</p>
                    </div>
                    <div   onClick={()=>{this.tanchuceng("暂未成立")}}>
                        <p><i className="iconfont icon-diannao"></i></p>
                        <p>工作室</p>
                    </div>
                </div>
                <List   className="my-list">
                    <Item
                        thumb={<i className="iconfont  icon-qianbao1"></i>}
                        arrow="horizontal">
                        分享赚钱
                    </Item>
                    <Item
                        thumb={<i className="iconfont  icon-qianbao1"></i>}
                        arrow="horizontal">
                        我的游戏
                    </Item>
                    <Item
                        thumb={<i className="iconfont  icon-qianbao1"></i>}
                        arrow="horizontal">
                        运营商
                    </Item>
                    <WhiteSpace size="lg" />
                    <PlaceHolder />
                    <Item
                        thumb={<i className="iconfont  icon-qianbao1"></i>}
                        arrow="horizontal">
                        扫一扫
                    </Item>
       
            </List>


            <div className="logindiv"  ref="logindiv" style={{display:'none'}}>
                <div className="loginparent">
                    {/* <div className="logincha">×</div> */}
                    <div className="logincolor">
                        <h3>登录</h3>
                        <div className="logininputdiv" >手机号：<input type="text" ref="phonenum" onBlur={this.userphone}  /></div>
                        <p className="logintishi">{this.state.tishi}</p>
                        <div className="logininputdiv" id="logininputdiv" >验证码：<input type="text"  ref="yzmInput" /><span className="loginyzmspan" onClick={this.getyzm}>获取验证码</span></div>
                        <p className="logintishi">{this.state.tishi2}</p>
                        <p className="loginp"><span className="loginpleft" onClick={this.loginhide}>取消</span><span className="loginpright" onClick={this.gotomine}>确认</span></p>
                       
                    </div>
                </div>
                
            </div>

            <div className="mineshadow" ref="mineshadow">{this.state.tishi}</div>
            </div>
        )
    }
}


