import React,{Component} from "react";
import {Route,Switch,Redirect} from "react-router-dom";
import url from "url"
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'; // ES6
import { NavBar, Icon,Button, List,Toast, WhiteSpace, WingBlank,Carousel,PullToRefresh } from 'antd-mobile';
import {connect} from "react-redux";
import { ajaxSend, getmovie,changeshoucang } from "../../actions";
@connect(
    state=>(
        {
            ...state
        }
    )
)
export class Myshoucang extends Component{


    componentWillMount(){
        const {location}=this.props
        console.log(url.parse(location.search,true).query.phonenum)
        var phonenum=url.parse(location.search,true).query.phonenum;
        const {dispatch} = this.props;
        dispatch(ajaxSend({
            config:{
                url:"/getusershoucang?phonenum="+phonenum,
                method:"get",
                
            },
            success(data){
                //shoucang
                dispatch(changeshoucang(data))
                console.log("ajaxSend")
                console.log(data);
               
            }
        }))

    }
    render(){
        const {match,history,shoucang}=this.props;
        return(
            <div>
                <NavBar
                    mode="light"
                    icon={<Icon type="left"  style={{color:'#666666'}}/>}
                    onLeftClick={() => history.goBack()}
                    rightContent={[
                        
                        <Icon key="1" type="ellipsis" style={{color:'#666666'}}/>,
                    ]}
                >
                {match.params.lei?match.params.lei:"我的收藏"}
                </NavBar>
                {
                   this.props.shoucang.length>0?
                   this.props.shoucang.map((shou,index)=>{
                       return(
                        <div className="feileidiv" key={index}>
                                    <div className="feileidivimg" onClick={()=>history.push("/app/listen/"+shou.trackId+"?vip="+shou.vip)}><div className="fenleii" ><i className="iconfont icon-bofang1"></i></div><img src={shou.coverMiddle} alt=""/></div>
                                    <div className="feileidivdiv">
                                        <h4>{shou.title.substring(0,20)+"..."}</h4>
                                        <p>{shou.nickname}</p>
                                        <p className="feileidivp"><span className="feileidivpleft">订阅量{shou.playtimes}万</span>
                                        
                                        </p>
                                    </div>
                                </div>
                            )
                      
                   })
                   
                   
                   
                   
                   :<div>暂未收藏任何内容</div>
                }
                
            </div>
        )
    }
}


