import React,{Component} from "react";
import {Route,Switch,Redirect} from "react-router-dom";
import "./listen.scss";
import url from "url";
import { NavBar, Icon,Modal, Button,Toast, WhiteSpace, WingBlank,ActionSheet,  } from 'antd-mobile';
const operation = Modal.operation;
const alert = Modal.alert;
const isIPhone = new RegExp('\\biPhone\\b|\\biPod\\b', 'i').test(window.navigator.userAgent);
let wrapProps;
if (isIPhone) {
  wrapProps = {
    onTouchStart: e => e.preventDefault(),
  };
}
import {connect} from "react-redux";
import {ajaxSendOne,changeoneTitle} from "../../actions";
@connect(
    state=>(
        {
            ...state
        }
    )
)
export class Listen extends Component{
    constructor() {
        super();
        this.state = {
          clicked: 'none',
          clicked1: 'none',
          clicked2: 'none',
        };
      }
    
      dataList = [
        { url: 'OpHiXAcYzmPQHcdlLFrc', title: '发送给朋友' },
        { url: 'wvEzCMiDZjthhAOcwTOu', title: '新浪微博' },
        { url: 'cTTayShKtEIdQVEMuiWt', title: '生活圈' },
        { url: 'umnHwvEgSyQtXlZjNJTt', title: '微信好友' },
        { url: 'SxpunpETIwdxNjcJamwB', title: 'QQ' },
      ].map(obj => ({
        icon: <img src={`https://gw.alipayobjects.com/zos/rmsportal/${obj.url}.png`} alt={obj.title} style={{ width: 36 }} />,
        title: obj.title,
      }));
    fenxiang=()=>{
        const {match,history,location}=this.props;
        console.log(location.search)
        console.log(url.parse(location.search,true).query.vip);
        
            ActionSheet.showShareActionSheetWithOptions({
                options: this.dataList,
                // title: 'title',
                message: '分享给好友：',
              },
              (buttonIndex) => {
                this.setState({ clicked1: buttonIndex > -1 ? this.dataList[buttonIndex].title : 'cancel' });
                // also support Promise
                // return new Promise((resolve) => {
                 
                //   setTimeout(resolve, 122);
                // });
              });
      
    }
    showToast() {
        Toast.info('This is a toast tips !!!');
      }
    componentWillMount(){
    // this.refs.listenbigdiv.className="listenbigdiv"
        
        const {match,history,location}=this.props;
        console.log(location.search)
        console.log(url.parse(location.search,true).query.vip);
        if(url.parse(location.search,true).query.vip!=1){
            console.log(match.params.id+"#######################");
            if(match.params.id){
                const {dispatch} = this.props;
                dispatch(ajaxSendOne({
                    config:{
                        url:"/getonetitle?trackId="+match.params.id,
                        method:"get",
                    },
                    success(data){
                        console.log("*&"+data);
                        dispatch(changeoneTitle(data))
                        console.log("ajaxSendOne")
                    }
                }))
            }
        }else{
            history.goBack();
           
            const alertInstance = alert('提示', '此为vip商品，需点击vip购买', [
                { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                { text: '确定', onPress: () => console.log('ok') },
              ]);
              setTimeout(() => {
                // 可以调用close方法以在外部close
                console.log('auto close');
                alertInstance.close();
              }, 500000);
        }
       
    }
    componentDidMount() {
        this.refs.listenbigdiv.className="listenbigdiv"
        setTimeout(() => {
          Toast.hide();
        }, 3000);
      }
    render(){
        const {history}=this.props;
        return(
            <div ref="listenbigdiv">
                <NavBar style={{ marginRight: '16px',color:'black' }} 
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() =>history.goBack() }
                    rightContent={[
                       
                        <Icon key="1" type="ellipsis" onClick={this.fenxiang} />,
                    ]}
                >
                播放页面
                </NavBar> 
                    {
                        this.props.onetitle.length>0?
                        this.props.onetitle.map((title,index)=>{
                            return(
                                <div key={index}>
                                    <h3 className="listenh1">{title.title.substring(0,10)+"..."}</h3>
                                    <div className="listenimg">
                                        <img src={title.coverMiddle} alt=""/>
                                    </div>
                                    <div className="listenaudio">
                                        <audio src={title.playUrl64} controls="controls" preload="auto"  loop="loop"></audio>
                                    </div>
                                </div>
                            )
                        })
                        :<h5>您未选中任何内容</h5>
                    }
                    
                  
                    
            </div>
        )
    }
}


