import React,{Component} from "react";
import {Route,Switch,Redirect,Link} from "react-router-dom";
import "./home.scss";
import { SearchBar, Button, WhiteSpace, WingBlank, Tabs,Carousel,NoticeBar,Icon  } from 'antd-mobile';
import "../../utils/swiper-3.3.1.min.js"


export  class Home extends Component{
    componentDidMount(){
        this.refs.bighomediv.style.animation="fadeInRight 0.9s"
        var mySwiper = new Swiper('.swiper-container',{
            
             })
    }

    componentDidUpdate(){
        var  mySwiper = new Swiper('.swiper-container',{
            
             })
     

    }
    state = {
        footList:[
            {img:require("../../../assets/images/book.png"),txt:"精彩小说",path:"/feilei/武侠"},
            {img:require("../../../assets/images/child.png"),txt:"睡前儿童",path:"/feilei/儿童"},
            {img:require("../../../assets/images/face.png"),txt:"悬疑小说",path:"/feilei/悬疑"},
            {img:require("../../../assets/images/jiangbei.png"),txt:"经典必听",path:"/feilei/文学"},
            {img:require("../../../assets/images/shanzi.png"),txt:"相声评书",path:"/feilei/相声"},
        ],
        homeimgs:[
            {img:require("../../../assets/images/bigxiangsheng.jpg")},
            
            {img:require("../../../assets/images/bigqinggan.jpg")},
            {img:require("../../../assets/images/bigyinyue.jpg")},
            {img:require("../../../assets/images/bigxuanyi.jpg")},
            {img:require("../../../assets/images/bigvip.jpg")},
            {img:require("../../../assets/images/bigwuxia.jpg")},
            {img:require("../../../assets/images/bigwenxue.jpg")},
            {img:require("../../../assets/images/bigertong.jpg")}
        ]
    }
    renderContent = tab =>(<div  style={{width:'100%',height:''}}>
                            <div>
                            <Link to={tab.path}>
                                {tab.show}
                            </Link> 
                            </div>
                            <div>
                            <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px',color:'#fc5959' } }}>
                                    听头条：七夕过去了，一起来分享分享你们的甜蜜生活吧！！！！！！！！！！！！！！！！！！
                                    </NoticeBar>
                            </div>
                            <div className="bgcshadow"></div>
                            <Link to={tab.path}>
                                <div className="footbox">
                                    <p className="youlike"><span className="youlikespan">猜你喜欢</span> <span className="youlikei">更多<i className='iconfont icon-jiantouyou'></i></span></p>
                                    <div className="gdgdiv">
                                        <span>
                                            <img style={{width:'50%'}} src={tab.img1} alt=""/>
                                            <img style={{width:'50%'}}  src={tab.img2} alt=""/>
                                        </span>
                                    </div>
                                </div> 
                            </Link> 
                            <Link to="/feilei/文学">
                                <div className="footbox">
                                    <p className="youlike"><span className="youlikespan">猜你喜欢</span> <span className="youlikei">更多<i className='iconfont icon-jiantouyou'></i></span></p>
                                    <div className="gdgdiv">
                                        <span>
                                            <img style={{width:'50%'}} src="http://fdfs.xmcdn.com/group42/M0A/77/EE/wKgJ81rB2NKgIIQ9AAGFQ4DdjBM639_web_large.jpg" alt=""/>
                                            <img style={{width:'50%'}}  src="http://fdfs.xmcdn.com/group12/M04/53/34/wKgDXFW4jrbgzdVnAAD6JFVr1tk360_web_large.jpg" alt=""/>
                                        </span>
                                    </div>
                                </div> 
                            </Link>
                            <Link to="/feilei/武侠">
                                <div className="footbox">
                                    <p className="youlike"><span className="youlikespan">猜你喜欢</span> <span className="youlikei">更多<i className='iconfont icon-jiantouyou'></i></span></p>
                                    <div className="gdgdiv">
                                        <span>
                                            <img style={{width:'50%'}} src="http://fdfs.xmcdn.com/group38/M01/34/C8/wKgJo1qClJ3RovLFAAAshcteBMM105_web_large.jpg" alt=""/>
                                            <img style={{width:'50%'}}  src="http://fdfs.xmcdn.com/group36/M02/44/16/wKgJUlouNz-DoQHeAAc1m2RPjB4562_web_large.jpg" alt=""/>
                                        </span>
                                    </div>
                                </div> 
                            </Link>
                            </div>);
    render(){
        const   show=(index)=>{
            return( <div className="showdiv">
                        <div className="color">
                        <img src={this.state.homeimgs[index].img} alt=""/>
                        </div>
                        <div className="xunhuandiv">
                            {
                                this.state.footList.map((foot,index)=>{
                                    return(
                                       <Link to={foot.path}  key={index}>
                                       <div className="showicondiv">
                                        <div className="imgdiv">
                                             <img src={foot.img} alt=""/>
                                        </div>
                                          <p>{foot.txt}</p>
                                        </div></Link>
                                        
                                    )
                                })
                            }
                            </div>
                    </div>)
        }
        const tabs = [
            {path:"/feilei/相声", title: '相声' ,show:show(0),img1:require("../../../assets/images/guodegang.jpg"),img2:require("../../../assets/images/dianying.jpg")},
            {path:"/feilei/情感", title: '情感生活',show:show(1),img1:require("../../../assets/images/ertong.jpg"),img2:require("../../../assets/images/qinggan.jpg")},
            {path:"/feilei/音乐", title: '音乐' ,show:show(2),img1:"http://fdfs.xmcdn.com/group48/M09/F9/27/wKgKnFt1EeLB8AKxAAGKvN0j9rY383_web_large.jpg",img2:"http://fdfs.xmcdn.com/group45/M07/1A/4A/wKgKlFt1Di-Bfot-AAEfUMihKqQ216_web_large.jpg"},
            {path:"/feilei/悬疑", title: '悬疑' ,show:show(3),img1:"http://fdfs.xmcdn.com/group32/M03/A2/30/wKgJnFm6gLOzBktSAAK9HRnbqms798_web_large.jpg",img2:"http://fdfs.xmcdn.com/group27/M09/9D/CE/wKgJR1jWk-TztJXHAACn8GdLedA695_web_large.jpg"},
            {path:"/feilei/vip", title: 'VIP' ,show:show(4),img1:"http://fdfs.xmcdn.com/group45/M05/60/3D/wKgKlFtUTHnAsUSkABOe6UgQpfY637_web_large.jpg",img2:"http://fdfs.xmcdn.com/group47/M05/F2/D2/wKgKm1tZ183D1c6qAAHqFFh-R34908_web_large.jpg"},
            {path:"/feilei/武侠", title: '武侠' ,show:show(5),img1:"http://fdfs.xmcdn.com/group26/M00/33/D7/wKgJWFjMEkXyYFuwAAFptlSeEPg301_web_large.jpg",img2:"http://fdfs.xmcdn.com/group41/M02/4C/23/wKgJ8lqgsNuhF4NDAAFYx5gTVwA959_web_large.jpg"},
            {path:"/feilei/文学", title: '文学' ,show:show(6),img1:"http://fdfs.xmcdn.com/group47/M0B/2F/A6/wKgKm1tej7_xM-m_AAGzYOmuyu8366_web_large.jpg",img2:"http://fdfs.xmcdn.com/group7/M05/24/7A/wKgDX1V_0tWRq-5uAADIWaPspmQ897_web_large.jpg"},
            {path:"/feilei/儿童", title: '儿童' ,show:show(7),img1:"http://fdfs.xmcdn.com/group46/M05/1B/C3/wKgKlltgAd3hgqYqAAJp6eJA0uU187_web_large.jpg",img2:"http://fdfs.xmcdn.com/group42/M08/96/A0/wKgJ9FqZJBTB6YweAALdZZ2Rcsc070_web_large.jpg"},
          ];
      const {history}=this.props
        return(
            <div ref="bighomediv">
              <header>
                <span className="headerspan"><i className="iconfont icon-youxiang"></i></span>
                <SearchBar onFocus={()=>history.push("/app/found")}  maxLength='20' className="searchInput" placeholder="Search"  />
                <div className="idiv">
                    <i className="iconfont icon-reloadtime"  onClick={()=>history.push("/app/listen")}></i>
                    <i className="iconfont icon-download" onClick={()=>history.push("/app/like")}></i>
                </div>
              </header>
              <div style={{marginTop:'50px'}}>
                 
                    <Tabs tabs={tabs} 
                    tabBarUnderlineStyle={{borderColor:'red',}} 
                    tabBarTextStyle={{color:'black',}}
                    tabBarInactiveTextColor={{color:'#666666'}}

                   >
                    {this.renderContent}
                    </Tabs>
                    
      </div>
            </div>
        )
    }
}


