import React,{Component} from "react";
import {Route,Switch,Redirect} from "react-router-dom";
import { SearchBar, Button, WhiteSpace, WingBlank, NavBar, Icon } from 'antd-mobile';
import "./found.scss";
import {connect} from "react-redux";
import {ajaxgetInputvalue,inputValue1} from "../../actions/index"
@connect(
    state=>(
        {
            ...state
        }
    )
)
export  class Found extends Component{
    state={
        showbool:"block"
    }
    searchtitle=()=>{
        var inputValue=this.refs.searchInput.state.value
        console.log(inputValue)
        const {dispatch,match} = this.props;

        
        dispatch(ajaxgetInputvalue({
            config:{
                url:"/getInputvalue",
                method:"get",
                params:{
                    inputValue:inputValue 
                }
               
            },
            success(data){
               
                if(data){
                    dispatch(inputValue1(data))
                }else{
                }
                console.log(data);
                console.log("ajaxSend")
            }
        }))
    }
    render(){
        const {history}=this.props;
        return(
            <div>
                <NavBar
                mode="light"
                icon={<Icon type="left" style={{color:'black'}} />}
                onLeftClick={() =>history.goBack()}
                defaultValue=""
                value=""
                >搜索</NavBar>
                <div>
                    <SearchBar placeholder="Search" ref="searchInput" onBlur={()=>{this.searchtitle()}} maxLength={20} />
                    <WhiteSpace />
                </div>
               
              {/* //inputValue */}
              {
                   this.props.inputtitle.length>0?
                   this.props.inputtitle.map((fen,index)=>{
                    return(
                            <div className="feileidiv" key={index}>
                                <div className="feileidivimg" onClick={()=>history.push("/app/listen/"+fen._id)}><div className="fenleii" ><i className="iconfont icon-bofang1"></i></div><img src={fen.coverMiddle} alt=""/></div>
                                <div className="feileidivdiv">
                                    <h4>{fen.title.substring(0,20)+"..."}</h4>
                                    <p>{fen.nickname}</p>
                                    <p className="feileidivp"><span className="feileidivpleft">订阅量{fen.playtimes}万</span><span className="feileidivpright">{fen.vip==1?<button>vip</button>:<button>订阅</button>}</span></p>
                                </div>
                            </div>
                        )
                    })
                   :<p>暂无数据</p>
                 
                  
                }
                  
            </div>
        )
    }
}


