import React,{Component} from "react";
import {Route,Switch,Redirect} from "react-router-dom";
import { ActionSheet, WingBlank, WhiteSpace, Button, Toast,Modal } from 'antd-mobile';
import "./like.scss";



const alert = Modal.alert;

import {connect} from "react-redux";
import { ajaxSend, getmovie,shoppingcarresult ,changecarcode} from "../../actions";
@connect(
    state=>(
        {
            ...state
        }
    )
)
export  class Like extends Component{
  state={
    allnum:0,
    allprice:0.00,
  }
  realcheck=(e,index)=>{
    this.setState({
      arr:this.props.carresult.map((car,i)=>{
        if(i==index){
          car.checked=!car.checked;
        }
        return car;
      })
    })
    this.tongji()
  }

  tongji=()=>{
    var allnum=0;
    var allprice=0;
    this.props.carresult.forEach((car,index)=>{
      if(car.checked===true){
       allnum+=1;
       allprice=allnum*10
      }
    })
    this.setState({
      allnum:allnum,
      allprice:allprice.toFixed(2)
    })
  }
  //deletetitle



  deletetitle=(_id)=>{
    var that=this;
    const {dispatch} = this.props;
    var phonenum=localStorage.phonenum;
    const alertInstance = alert('删除', '您确定删除此项吗', [
      { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
      { text: '确定', onPress: () =>(
        dispatch(ajaxSend({
          config:{
              url:"/deletetitle?_id="+_id+"&phonenum="+localStorage.phonenum,
              method:"get",
          },
          success(data){
            dispatch(shoppingcarresult(data))
            that.tongji()
              console.log("ajaxSend")
              console.log(data);
             
          }
      }))
      ) },
    ]);
    setTimeout(() => {
      // 可以调用close方法以在外部close
      console.log('auto close');
      alertInstance.close();
    }, 500000);


   
   

  }

  componentWillMount(){
    
    var that=this;
    const {dispatch} = this.props;
    var phonenum=localStorage.phonenum;
    if(phonenum){
      dispatch(ajaxSend({
        config:{
            url:"/willshopping?phonenum="+phonenum,
            method:"get",
        },
        success(data){
            dispatch(shoppingcarresult(data))
            console.log("ajaxSend")
            console.log(data);
           
        }
    }))
    }else{
      const {dispatch} = this.props;
      dispatch(changecarcode("您还未登录"))
    }
  }
  
    render(){
      return(
        <div>
          <h3 className="likeh3">我的购物车</h3>
          <div className="likebottom">
            {
              this.props.carresult.length>0?
              this.props.carresult.map((carcar,index)=>{
                return(
                  <div key={index}>
                        <div className="likebigdiv">
                            <div className="checkboxdiv"><input type="checkbox" checked={carcar.checked} onClick={(e)=>this.realcheck(e,index)}/></div>
                            <span className="likeSpan"><img src={carcar.coverMiddle} alt=""/></span>
                            <div className="likemidlediv">
                              <h5>{carcar.title.substring(0,5)+"..."}</h5>
                              <p>{carcar.nikename}</p>
                              <p>{carcar.playtimes}人购买</p>
                              <p>数量：全一期</p>
                              <p>价格：<span>10</span>RMB</p>
                            </div>
                            <p className="middlep"><span onClick={()=>{this.deletetitle(carcar._id)}} >删除</span></p>
                         </div>
                  </div>
                )
              })
              
              
              :<p>您还未购买商品</p>
            }
          </div>
         
          

          <div className="likefootdiv">
              <p className="likefootp" id="likebuy">立即购买</p>
              <p className="likefootp">金额：<span>{this.state.allprice}</span></p>
              <p className="likefootp">数量：<span>{this.state.allnum}</span></p>
          </div>
          <div className="mineshadow" ref="mineshadow">您还未登录，请点击登录</div>
        </div>
      )
    }
}


